import React from 'react';
import OleHeader from './oleHeader';
import OleBody from './oleBody';
import OleFooter from './oleFooter';

export default class Olemoo extends React.Component {

  constructor() {
    super();
    /** Define and bind handles for inter-component action */
    this.handleToggleBtn = this.handleToggleBtn.bind(this);

    /** Define states for our website here */
    this.state = {
      device: "mobile",
      userLoggedIn: 0,
      toggleSidebar: 0
    }
  }

  /**
   * Entry Point for rendering.
   *  1. Here we query current window dimensions to determine device for which website shall be rendered.
   *  2. Next, we add an event listenet to update window dimensions upon change in order to accommodate responsive behavior.
   */
  componentDidMount() {
    this.updateDimensions();
    window.addEventListener("resize", this.updateDimensions.bind(this));
  }

  /**
   * Exit Point for rendering.
   * Remove event listener
   */
  componentWillUnmount() {
    window.removeEventListener("resize", this.updateDimensions.bind(this));
  }

  /**
   * Calculate & Update state of new dimensions. We will use bootstrap4 breakpoints
   * xs = Extra small <576px, Max container width None (auto)
   * sm = Small ≥576px, Max container width 540px
   * md = Medium ≥768px, Max container width 720px
   * lg = Large ≥992px ,Max container width 960px
   * xl = Extra large ≥1200px, Max container width 1140px
   */

  updateDimensions() {
    if( window.innerWidth < 768 ) {
      this.setState({ device: "mobile" });
      this.setState({ toggleSidebar: 0}); /** Hide Sidebar */
    } else if( window.innerWidth < 992) {
      this.setState({ device: "tablet" });
      this.setState({ toggleSidebar: 0}); /** Hide Sidebar */
    } else {
      this.setState({device: "desktop"});
      this.setState({ toggleSidebar: 1}); /** Show Sidebar */
    }
  }

  /** Function handler to update settings when user clicks on Toggle button to hide/unhide sidebar
   *  The Navbar menu options hide automatically on collapse with CSS settings. Threfore, this handler 
   *  should be passed as proprty to classes that will take action on toggle button click.
   */
  handleToggleBtn() {
    this.setState({toggleSidebar: !this.state.toggleSidebar});
  }

  render() {
    return (
      /** Olemoo Layout - Multipage React Website */
      <div>
        <OleHeader handleToggleBtn={this.handleToggleBtn}/>
        <OleBody device={this.state.device} toggleSidebar={this.state.toggleSidebar} />
        <OleFooter />
      </div>
    )
  }
}