import React from 'react';
import OleSidebar from './oleSideBar';
import OleRouter from './oleRouter';
import './css/oleBody.css';

export default class OleBody extends React.Component {
  
  render() {
  const toggleSidebar = this.props.toggleSidebar;
  const device = this.props.device;

  if(toggleSidebar) {
    if(device !== "desktop") {
      document.body.style.position = "fixed";
    } else {
      document.body.style.position = "";
    }
  } else {
    document.body.style.position = "";
  }

  return(
    <div className="container-fluid">
      <div className="row oleBody">
        <OleSidebar toggleSidebar={toggleSidebar} />
        <div className="col oleContent">
          <OleRouter />
        </div>
      </div>
    </div>
    )
  }
}