import React from 'react';
import { Switch, Route } from 'react-router-dom';

/** Import all pages of olemoo website */
import OleHome from './pages/oleHome';
import OleAboutUs from './pages/oleAboutus';
import OleContactUs from './pages/oleContact';
import OleDeliveryPartners from './pages/oleDeliveryPartners';
import OleFAQs from './pages/oleFaq';
import OleGhee from './pages/oleGhee';
import OleMilk from './pages/oleMilk';
import OleOrders from './pages/oleOrders';
import OlePaneer from './pages/olePaneer';
import OleProcess from './pages/oleProcess';

export default class OleRouter extends React.Component {
  render() {
    return(
      <div>
        <Switch>
          <Route exact path="/" component={OleHome}></Route>
          <Route path="/olemoo_website" component={OleHome}></Route>
          <Route path="/aboutus" component={OleAboutUs}></Route>
          <Route path="/contactus" component={OleContactUs}></Route>
          <Route path="/deliverypartners" component={OleDeliveryPartners}></Route>
          <Route path="/faqs" component={OleFAQs}></Route>
          <Route path="/products/ghee" component={OleGhee}></Route>
          <Route path="/products/milk" component={OleMilk}></Route>
          <Route path="/orders" component={OleOrders}></Route>
          <Route path="/products/paneer" component={OlePaneer}></Route>
          <Route path="/process" component={OleProcess}></Route>
        </Switch>
      </div>
    )
  }
}